import React, { Component } from 'react';


const NewsListItem = () => (
	<div className="news-list-item">
      	<div className="news-list-item-left">
      		<a href="#/" target="_blank"><img src={require("../img/timthumb.jpg")} alt=" | zhongqiu" /></a>
      	</div>
      	<div className="news-list-item-right">
      		<h2><a href="#/" target="_blank"> | zhongqiu</a></h2>
      		<div>2017-09-29 102</div>
      		<p>...</p>
      	</div>
	</div>
);



const NewsList = () => (
	<div className="block-content news-list">
      	<NewsListItem />
      	<NewsListItem />
      	<NewsListItem />
      	<NewsListItem />
      	<NewsListItem />
      	<NewsListItem />
      	<NewsListItem />
	</div>
);

export default NewsList;


